.Reporteria-Leyenda {
  display: inline-block;
  margin: 5px 10px;
  width: 300px;
  padding: 10px;
  background-color: #d1920085;
  font-size: small;
  border-radius: 10px;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  border: solid 1px #d1920085;
}

.Reporteria-Leyenda-Container {
  text-align: center;
}

.Reporteria-ContenedorTemas {
  min-width: 600px;
  width: 800px;
  border-radius: 30px;
  margin: 20px;
  background-color: white;
  border: solid 1px;
  -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.75);
  text-align: center;
  position: relative;
  display: inline-block;
}

.Reporteria-ContenedorEstadisticas {
  display: inline;
  margin: 0px 10px;
  min-width: 300px;
  text-align: center;
}

.Reporteria-DropdownMenu {
  display: inline-block;
  width: 300px;
  margin: 30px;
}

.Reporteria-DropdownMenuPlaceHolder {
  background-color: teal;
}

.Reporteria-LeyendaEstadisticas {
  display: inline-block;
  margin: 5px 10px;
  width: 300px;
  padding: 10px;
  background-color: #d1920085;
  font-size: small;
  border-radius: 10px;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  border: solid 1px #d1920085;
}

.Reporteria-Descarga:hover {
  cursor: pointer;
}
