:root {
	--sidebar-width: 10em;
}
main {
	margin-left: var(--sidebar-width);
	padding: 1em;
}
.alert-notification {
	animation: notification 2s ease-out -2s infinite alternate forwards;
	-webkit-animation: notification 2s ease-out -2s infinite alternate forwards;
}
@-webkit-keyframes notification {
	from {
		background-color: #253646;
	}
	to {
		background-color: #d81e05;
	}
}
@keyframes notification {
	from {
		background-color: #253646;
	}
	to {
		background-color: #d81e05;
	}
}
.notification {
	position: fixed;
	vertical-align: middle;
	top: 10px;
	left: 20px;
	padding: 20px;
	border-radius: 5px;
	font-size: large;
	-webkit-box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
	box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
	z-index: 1000;
}
.alert {
